.bg-cyberpunk-grid {
  background-color: #10111a;
  background-image: linear-gradient(0deg, rgba(0,255,255,0.07) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0,255,255,0.07) 1px, transparent 1px);
  background-size: 32px 32px;
}
.cyberpunk-font {
  font-family: 'Orbitron', 'Inter', 'Segoe UI', 'Arial', sans-serif;
  letter-spacing: 0.03em;
}
.cyberpunk-glass {
  background: rgba(16,17,26,0.7);
  border: 1.5px solid #00fff744;
  border-radius: 1.25rem;
  box-shadow: 0 0 24px #00fff722, 0 0 48px #7f00ff11;
  backdrop-filter: blur(10px);
}
.cyberpunk-card {
  background: rgba(16,17,26,0.92);
  border: 1.5px solid #00fff7;
  border-radius: 1.25rem;
  box-shadow: 0 0 24px #00fff733, 0 0 48px #7f00ff22;
  padding: 2rem;
  color: #e0eaff;
  backdrop-filter: blur(8px);
}
.cyberpunk-btn {
  background: transparent;
  border: 2px solid #00fff7;
  color: #00fff7;
  border-radius: 0.75rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  box-shadow: 0 0 8px #00fff7, 0 0 24px #7f00ff33;
  transition: all 0.18s;
}
.cyberpunk-btn:hover {
  background: #00fff722;
  color: #fff;
  border-color: #7f00ff;
  box-shadow: 0 0 16px #7f00ff, 0 0 32px #00fff7;
}
.cyberpunk-input {
  background: #181a24;
  border: 1.5px solid #00fff7;
  color: #fff;
  border-radius: 0.75rem;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  box-shadow: 0 0 8px #00fff733;
  transition: border 0.18s, box-shadow 0.18s;
}
.cyberpunk-input:focus {
  border-color: #7f00ff;
  box-shadow: 0 0 16px #7f00ff, 0 0 32px #00fff7;
  outline: none;
}
.cyberpunk-modal {
  background: rgba(16,17,26,0.98);
  border: 2px solid #00fff7;
  border-radius: 1.5rem;
  box-shadow: 0 0 32px #00fff7, 0 0 64px #7f00ff44;
  color: #fff;
  padding: 2.5rem 2rem;
}
.cyberpunk-toast {
  background: #181a24;
  border: 2px solid #00fff7;
  color: #fff;
  border-radius: 0.75rem;
  box-shadow: 0 0 16px #00fff7, 0 0 32px #7f00ff33;
  padding: 1rem 1.5rem;
  font-weight: 600;
}
.shadow-cyberpunk-glow {
  box-shadow: 0 0 12px 2px #00fff7, 0 0 32px 4px #7f00ff33;
}
.drop-shadow-cyberpunk {
  filter: drop-shadow(0 0 6px #00fff7) drop-shadow(0 0 12px #7f00ff);
}
.text-gradient-cyberpunk {
  background: linear-gradient(135deg, #00fff7 0%, #7f00ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
@keyframes cyberpunk-glow {
  0% {
    box-shadow: 0 0 8px #00fff7, 0 0 24px #7f00ff33;
  }
  50% {
    box-shadow: 0 0 24px #7f00ff, 0 0 48px #00fff7;
  }
  100% {
    box-shadow: 0 0 8px #00fff7, 0 0 24px #7f00ff33;
  }
}
.animate-cyberpunk-glow {
  animation: cyberpunk-glow 2s ease-in-out infinite;
}
